import { Observable } from 'rxjs';
import { EditorService, UndoService, CanvasService } from '../services';
import { EditorToolBase } from './tool.base';
import { takeUntil, throttleTime } from 'rxjs/operators';
import * as Hammer from 'hammerjs';
import * as paper from 'paper';

interface DrawPoint {
    x: number;
    y: number;
}

enum TRANSFORM_TYPES {
    RESIZE,
    MOVE
}

export abstract class EditorDrawToolBase extends EditorToolBase {
    protected abstract canvasService: CanvasService;
    protected abstract undoService: UndoService;
    protected abstract editorService: EditorService;
    protected abstract toolParams: any;

    hummerManager: HammerManager;
    isDrawing: boolean;
    drawPaths = [];
    hitPaths = [];
    exportPaths = [];
    drawPoints: DrawPoint[] = [];
    centerPoint: paper.Point;
    drawStartPoint: paper.Point;

    isEnabledTransform = true;

    transfromType: TRANSFORM_TYPES;
    transfromStartPoint: paper.Point;
    transfromStartSize: paper.Rectangle;
    transformWidth: number;
    transformRotation: number;
    transformCenter: paper.Point;
    transformOffsetPoint: paper.Point;
    isMirrorTransform = false;
    isLeftSizeTransform = false;

    addSubscriptions(isTransformMode?: boolean) {
        super.addSubscriptions();

        const pan = new Hammer.Pan({ threshold: 0 });
        const press = new Hammer.Press({ time: 1 });
        this.hummerManager = new Hammer.Manager(this.canvasService.getDrawElement());
        this.hummerManager.add([pan, press]);

        this.hummerManager.on(isTransformMode ? 'press' : 'panstart', (e) => {
            const point = this.canvasService.normalizePoint(e.center);
            isTransformMode ? this.transformStart(point) : this.drawStart(point);
        });

        const pan$ = Observable.create((observer) => {
            this.hummerManager.on('pan', e => observer.next(e));
        }).pipe(throttleTime(30));
        pan$.pipe(takeUntil(this.clear$))
            .subscribe(e => {
                const point = this.canvasService.normalizePoint(e.center);
                isTransformMode ? this.transform(point) : this.draw(point);
            });

        this.hummerManager.on('panend', () => isTransformMode ? this.transformEnd() : this.drawEnd());
    }

    clearSubscriptions() {
        super.clearSubscriptions();

        this.hummerManager.destroy();
    }

    drawStart({x, y}: DrawPoint) {
        this.drawPoints.push({x, y});
        this.isDrawing = true;
        this.centerPoint = this.getCanvasCenterPoint();
        this.drawStartPoint = this.createPoint(x, y);
        const drawAngle = this.getDrawAngle(this.drawStartPoint);
        this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
            const params = { zoneIndex, zoneAngle, zoneStep, drawPoint: this.drawStartPoint, drawAngle };
            const [mainPoint, mirrorPoint] = this.getDrawPoints(this.drawStartPoint, zoneStep, zoneAngle, drawAngle);
            this.drawPaths.push([
                this.drawZoneStartIteration({drawZonePoint: mainPoint, ...params}),
                this.isDrawMirrored ? this.drawZoneStartIteration({drawZonePoint: mirrorPoint, ...params}) : null
            ]);
        });

        this.draw({x, y});
    }

    abstract drawZoneStartIteration(params);


    draw({x, y}: DrawPoint) {
        if (this.isDrawing) {
            this.drawPoints.push({x, y});

            const drawPoint = this.createPoint(x, y);
            const drawAngle = this.getDrawAngle(drawPoint);
            this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
                const params = { zoneIndex, zoneAngle, zoneStep, drawPoint, drawAngle };
                const [mainPoint, mirrorPoint] = this.getDrawPoints(drawPoint, zoneStep, zoneAngle, drawAngle);
                let [zonePath, mirrorPath] = this.drawPaths[zoneIndex];
                if (zonePath) {
                    zonePath = this.drawZoneIteration({ isMirror: false, drawZonePath: zonePath, drawZonePoint: mainPoint, ...params});
                }
                if (mirrorPath) {
                    mirrorPath = this.drawZoneIteration({ isMirror: true, drawZonePath: mirrorPath, drawZonePoint: mirrorPoint, ...params});
                }

                this.drawPaths[zoneIndex] = [zonePath, mirrorPath];
            });
            this.canvasService.drawProject.view.update();
        }
    }

    abstract drawZoneIteration(params);


    drawEnd() {
        this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
            const params = {zoneIndex, zoneAngle, zoneStep};
            const [zonePath, mirrorPath] = this.drawPaths[zoneIndex];
            const exportPaths = [null, null];
            const hitPaths = [null, null];
            if (zonePath) {
                exportPaths[0] = this.drawZoneEndIteration({ drawZonePath: zonePath, ...params });
                hitPaths[0] = this.createHitRect(zonePath);
            }
            if (mirrorPath) {
                exportPaths[1] = this.drawZoneEndIteration({ drawZonePath: mirrorPath, ...params });
                hitPaths[1] = this.createHitRect(mirrorPath);
            }

            this.exportPaths[zoneIndex] = exportPaths;
            this.hitPaths[zoneIndex] = hitPaths;
        });


        this.canvasService.drawProject.view.update();
        this.isDrawing = false;

        if (this.isEnabledTransform) {
            this.clearSubscriptions();
            this.addSubscriptions(true);
        } else {
            this.finishDraw();
        }
    }

    abstract drawZoneEndIteration(params);


    transformStart(point: DrawPoint) {
        let isHited = false;
        this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
            if (isHited) {
                return;
            }

            const [zonePath, mirrorPath] = this.hitPaths[zoneIndex];
            if (zonePath) {
                const hitResult = zonePath.hitTest(point);
                if (hitResult) {
                    isHited = true;
                    this.isMirrorTransform = false;
                    this.hitedPath(point, zonePath, hitResult);
                }
            }
            if (mirrorPath) {
                const hitResult = mirrorPath.hitTest(point);
                if (hitResult) {
                    isHited = true;
                    this.isMirrorTransform = true;
                    this.hitedPath(point, mirrorPath, hitResult);
                }
            }
        });
        const hitResults = paper.project.hitTestAll(new paper.Point(point));
        console.log('hitResults: ', hitResults);
        console.log('isHited: ', isHited);
        if (!isHited) {
            this.finishDraw();

            this.clearSubscriptions();
            this.addSubscriptions();
        }
    }

    finishDraw() {
        this.hitPaths.forEach(([zonePath, mirrorPath]) => {
            if (zonePath) {
                zonePath.remove();
            }
            if (mirrorPath) {
                mirrorPath.remove();
            }
        });
        this.canvasService.drawProject.view.update();

        const drawStep = this.getState(this.drawPoints, this.toolParams);
        const drawStepKey = this.editorService.addUserDrawStep(drawStep);
        const allDrawPaths = this.getAllPaths(this.drawPaths);
        const allExportPaths = this.getAllPaths(this.exportPaths);
        this.canvasService.addPaperItems(drawStepKey, allDrawPaths, allExportPaths);
        this.undoService.addCommand({
            undo: () => {
                this.editorService.removeUserDrawStep(drawStepKey);
                this.canvasService.removePaperItems(drawStepKey);
            },
            redo: () => {
                this.editorService.addUserDrawStep(drawStep, drawStepKey);
                this.canvasService.addPaperItems(drawStepKey, allDrawPaths, allExportPaths);
            }
        });

        this.drawPaths = [];
        this.exportPaths = [];
        this.drawPoints = [];
        this.hitPaths = [];
    }


    transform({x, y}: DrawPoint) {
        const tPoint = this.createPoint(x, y);
        // tPoint.rotate()
        const point = tPoint.subtract(this.transformOffsetPoint);
        if (this.transfromType === TRANSFORM_TYPES.MOVE) {
            const drawAngle = this.getDrawAngle(point);
            console.log('drawAngle: ', drawAngle);
            this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
                const [mainPoint, mirrorPoint] = this.getDrawPoints(point, zoneStep, zoneAngle, drawAngle);
                const [zoneHitPath, mirrorHitPath] = this.hitPaths[zoneIndex];
                if (zoneHitPath) {
                    zoneHitPath.position = this.isMirrorTransform ?  mirrorPoint : mainPoint;
                }
                if (mirrorHitPath) {
                    console.log(mirrorHitPath.position, mirrorPoint);
                    mirrorHitPath.position = this.isMirrorTransform ?  mainPoint : mirrorPoint;
                }

                const [zonePath, mirrorPath] = this.drawPaths[zoneIndex];
                if (zonePath) {
                    zonePath.position = this.isMirrorTransform ?  mirrorPoint : mainPoint;
                }
                if (mirrorPath) {
                    mirrorPath.position = this.isMirrorTransform ?  mainPoint : mirrorPoint;
                }
            });
            this.canvasService.drawProject.view.update();
        } else {
            // const point = tPoint.subtract(this.transformOffsetPoint);
            const vector = tPoint.subtract(this.transfromStartPoint);
            console.log('vector: ', vector);
            const distance = (vector.x < 0 ? -1 : 1) * tPoint.getDistance(this.transfromStartPoint, false);
            const drawAngle = this.getDrawAngle(tPoint);
            const transformRotation = point.subtract(this.transformCenter).angle;
            const transformWidth = this.isLeftSizeTransform
                ? this.transfromStartSize.width - distance
                : this.transfromStartSize.width + distance;
            console.log('transformWidth: ', transformWidth);
            const scale = transformWidth / this.transformWidth;
            const rotation = transformRotation - this.transformRotation;
            this.transformWidth = transformWidth;
            this.transformRotation = rotation;
            this.transformRotation++;
            console.log('');
            // console.log('width: ', width);
            // console.log(' this.transfromStartSize.width: ',  this.transfromStartSize.width);
            // console.log(' this.transformScale: ',  this.transformScale);

            this.zonesIteration((zoneIndex, zoneAngle, zoneStep) => {
                const [mainPoint, mirrorPoint] = this.getDrawPoints(point, zoneStep, zoneAngle, drawAngle);
                const [zoneHitPath, mirrorHitPath] = this.hitPaths[zoneIndex];
                const [zonePath, mirrorPath] = this.drawPaths[zoneIndex];
                if (zoneHitPath) {
                    console.log('zoneHitPath: ', zoneHitPath);
                    // zoneHitPath.rotate(rotation, zoneHitPath.position);
                    zoneHitPath.scale(scale);
                }
                if (mirrorHitPath) {
                    mirrorHitPath.scale(scale);
                }




                if (zonePath) {
                    // zonePath.rotate(rotation, zoneHitPath.position);
                    zonePath.scale(scale);
                }

                if (mirrorPath) {
                    mirrorPath.scale(scale);
                }
            });
            this.canvasService.drawProject.view.update();
            console.log('distance: ', distance);
        }
    }


    transformEnd() {
        console.log('transform end');
    }

    hitedPath({x, y}: DrawPoint, path: paper.Raster, hitResult: paper.HitResult) {
        console.log('hitResult.type: ' , hitResult.type);
        if (hitResult.type === 'segment') {
            this.transfromType = TRANSFORM_TYPES.RESIZE;
        } else {
            this.transfromType = TRANSFORM_TYPES.MOVE;
        }
        this.transfromStartPoint = this.createPoint(x, y);
        this.transfromStartSize = path.bounds.clone();
        this.transformWidth = this.transfromStartSize.width;
        this.transformRotation = path.rotation;
        this.transformCenter = path.position;
        this.transformOffsetPoint = this.transfromStartPoint.subtract(this.transformCenter);
        this.isLeftSizeTransform = this.transformCenter.x > this.transfromStartPoint.x;
    }

    createHitRect(path) {
        const size = path instanceof paper.Raster ? path.size : new paper.Size(path.bounds.width, path.bounds.height);
        const rect = new paper.Path.Rectangle(path.position, size);
        rect.transform(path.matrix);
        rect.position = path.position;
        rect.fillColor = new paper.Color('red');
        rect.opacity = 0;
        rect.selected = true;
        rect.selectedColor = new paper.Color('red');

        return rect;
    }


    getCanvasCenterPoint() {
        const size = this.canvasService.getCanvasSize();
        return this.createPoint(size.width / 2, size.height / 2);
    }

    zonesIteration(cb: (zoneIndex: number, zoneAngle: number, zoneStep: number) => void) {
        const step = 360 / this.zonesCount;

        for (let i = 0; i < this.zonesCount; i++) {
            cb(i, step * i, step);
        }
    }

    createPoint(x: number, y: number): paper.Point {
        return new paper.Point(x, y);
    }

    getDrawAngle(drawPoint: paper.Point) {
        const centerPoint = this.getCanvasCenterPoint();
        const size = this.canvasService.getCanvasSize();
        const halfHeight = size.height / 2;
        const point2 = new paper.Point(drawPoint.y > halfHeight ? 0 : size.height, halfHeight);
        const vector1 = drawPoint.subtract(centerPoint);
        const vector2 = point2.subtract(centerPoint);
        return vector1.getAngle(vector2);
    }

    getDrawPoints(drawPoint: paper.Point, zoneStep: number, zoneAngle: number, drawAngle: number): [paper.Point, paper.Point] {
        const diff = drawAngle + zoneStep / 2;
        return [
            this.getDrawPointWithRotation(drawPoint, zoneAngle),
            this.getDrawPointWithRotation(drawPoint, zoneAngle + diff * 2)
        ];
    }

    getDrawPointWithRotation(drawPoint: paper.Point, rotateAngle: number): paper.Point {
        return drawPoint.rotate(rotateAngle, this.centerPoint);
    }

    getAllPaths(paths) {
        return paths.reduce((list, zonePaths) => {
            zonePaths.forEach(p => p && list.push(p));
            return list;
        }, []);
    }
}
